import { request } from './index';

// 登录接口
export const login = function (data) {
  return request({
    url: '/app/v1_0/authorizations',
    method: 'post',
    data,
  });
};
// 验证码接口
export const getCode = function (mobile) {
  return request({
    url: `/app/v1_0/sms/codes/${mobile}`,
  });
};

// 获取登录用户的信息
export const getCurrentUser = function () {
  return request({
    url: '/app/v1_0/user',
  });
};
