<template>
  <div class="info-container">
    <!-- 登录后的 -->
    <van-cell-group v-if="userToken" class="banner-info">
      <!-- 头像区域 -->
      <van-cell :border="false" class="info-title" center>
        <van-image
          class="avitor"
          slot="icon"
          round
          contain
          fit="cover"
          :src="myInfo.photo"
        />
        <div slot="title" class="info-name">{{ myInfo.name || `花花` }}</div>
        <van-button to="/user/profile" size="small" round class="info-set"
          >编辑资料</van-button
        >
      </van-cell>

      <!-- 中间区域 -->
      <van-grid :border="false" class="info-data">
        <van-grid-item>
          <div slot="text" class="text-wrap">
            <div class="num">{{ myInfo.art_count || 0 }}</div>
            <div class="text">文章</div>
          </div>
        </van-grid-item>
        <van-grid-item>
          <div slot="text" class="text-wrap">
            <div class="num">{{ myInfo.follow_count || 0 }}</div>
            <div class="text">关注</div>
          </div>
        </van-grid-item>
        <van-grid-item>
          <div slot="text" class="text-wrap">
            <div class="num">{{ myInfo.fans_count || 0 }}</div>
            <div class="text">粉丝</div>
          </div>
        </van-grid-item>
        <van-grid-item>
          <div slot="text" class="text-wrap">
            <div class="num">{{ myInfo.like_count || 0 }}</div>
            <div class="text">获赞</div>
          </div>
        </van-grid-item>
      </van-grid>
    </van-cell-group>
    <!-- 未登录的 -->
    <van-cell-group
      @click="
        $router.push({
          name: `Login`,
          query: {
            toPath: `/myInfo`,
          },
        })
      "
      v-else
      class="not_login"
    >
      <!-- 头像区域 -->
      <div>
        <img src="./login.png" alt="" />
      </div>
      <div class="login_text">登录 / 注册</div>
    </van-cell-group>

    <!-- 收藏 历史 -->
    <van-grid :column-num="2" class="nav_grid mb_4">
      <van-grid-item icon-prefix="iconfont" icon=" iconshoucang" text="收藏" />
      <van-grid-item
        icon-prefix="iconfont"
        icon=" iconlishi"
        text="历史"
        url="/vant/mobile.html"
      />
    </van-grid>
    <!-- 消息 -->
    <van-cell title="消息通知" is-link to="/" />
    <!-- 客服 -->
    <van-cell title="智能客服" is-link to="/user/chat" class="mb_4" />
    <!-- 推出登录 -->
    <van-cell
      v-if="userToken"
      title="退出登录"
      class="logout_cell"
      @click="logout"
    />
  </div>
</template>

<script>
import eventBus from '@/utils/eventBus.js';
import io from 'socket.io-client';
import { mapState, mapMutations } from 'vuex';
import { removeItem, setItem } from '@/utils/storage.js';
import { getCurrentUser } from '../../api/user';

console.log(eventBus);
export default {
  name: 'Myinfo',
  data() {
    return {
      myInfo: {},
    };
  },
  computed: {
    ...mapState(['userToken']),
  },
  methods: {
    ...mapMutations(['setToken']),
    // 退出登录
    async logout() {
      try {
        await this.$dialog.confirm({
          title: '退出提示',
          message: '是否确认退出',
        });
        this.setToken(null);
        this.myInfo = null;
        removeItem('active');
        removeItem('chat_msg');
      } catch (e) {
        console.log(e);
      }
    },
    // 获取当前用户信息
    async getCurrentUserInfo() {
      try {
        const res = await getCurrentUser();
        if (res.status == 200) {
          this.myInfo = res.data.data;
          setItem('userPhoto', res.data.data.photo);
        }
      } catch (e) {
        console.log(e);
      }
    },
  },
  activated() {},
  updated() {},
  created() {
    eventBus.$on('setUserName', (name) => {
      this.myInfo.name = name;
    });
    this.$bus = function (data) {};
    if (this.userToken) {
      this.getCurrentUserInfo();
    }
  },
};
</script>

<style lang="less" scoped>
.info-container {
  .mb_4 {
    margin-bottom: 4px;
  }

  .not_login {
    height: 180px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url("./banner.png") no-repeat center/cover;
    img {
      width: 66px;
      height: 66px;
    }
    .login_text {
      color: #fff;
      font-size: 14px;
    }
  }

  .banner-info {
    padding-bottom: 22px;
    background: url("./banner.png") no-repeat center/cover;
    .info-title {
      padding: 38px 16px 16px 15px;
      background-color: unset;
      .avitor {
        box-sizing: border-box;
        width: 66px;
        height: 66px;
        border: 1px solid #fff;
      }
      .info-name {
        margin-left: 11px;
        color: #fff;
        font-size: 15px;
      }
      .info-set {
        height: 16px;
        font-size: 10px;
        color: #666;
      }
    }
    .info-data {
      .van-grid-item {
        height: 65px;
        color: #fff;
        .text-wrap {
          display: flex;
          flex-direction: column;
          align-items: center;
          .num {
            font-size: 16px;
          }
          .text {
            font-size: 11px;
          }
        }
      }
    }
    /deep/ .van-grid-item__content {
      background-color: unset;
    }
  }

  .nav_grid {
    /deep/.van-grid-item {
      height: 70px;
      .iconfont {
        font-size: 22px;
        &.iconshoucang {
          color: #eb5253;
        }
        &.iconlishi {
          color: #ff9ded;
        }
      }
      .van-grid-item__text {
        font-size: 14px;
        color: #333;
      }
    }
  }

  .logout_cell {
    text-align: center;
    color: #d86262;
  }
}
</style>
